.wp-pagenavi {
  display: flex;
  justify-content: center;
  gap: 0.625rem;
}

.wp-pagenavi .previouspostslink {
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  border: 1px solid #408F95;
  position: relative;
  transition: 0.3s;
  margin-right: 2.5rem;
}

.wp-pagenavi .previouspostslink::before {
  content: "";
  width: 0.5rem;
  height: 1rem;
  background-image: url(../images/common/page-prev.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s;
}

.wp-pagenavi .previouspostslink:hover {
  background-color: #408F95;
}

.wp-pagenavi .previouspostslink:hover::before {
  background-image: url(../images/common/page-prev-hover.svg);
}

.wp-pagenavi .nextpostslink {
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  border: 1px solid #408F95;
  position: relative;
  transition: 0.3s;
  margin-left: 2.5rem;
}

.wp-pagenavi .nextpostslink::before {
  content: "";
  width: 0.5rem;
  height: 1rem;
  background-image: url(../images/common/page-next.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s;
}

.wp-pagenavi .nextpostslink:hover {
  background-color: #408F95;
}

.wp-pagenavi .nextpostslink:hover::before {
  background-image: url(../images/common/page-next-hover.svg);
}

.wp-pagenavi a, .wp-pagenavi span {
  display: inline-flex;
  width: 2.375rem;
  height: 2.375rem;
  background: #fff;
  border: 1px solid #408F95;
  font-size: 1rem;
  font-family: "Lato", sans-serif;
  color: #408F95;
  font-weight: 700;
  padding-top: 0.625rem;
  padding-right: 0.875rem;
  padding-bottom: 0.625rem;
  padding-left: 0.875rem;
  line-height: 1;
  transition: all 0.15s linear;
}

.wp-pagenavi a:hover {
  background-color: #408F95;
  color: #fff;
  opacity: 1;
}

.wp-pagenavi span.current {
