@use 'global' as *;

.wp-pagenavi {
  display: flex;
  justify-content: center;
  gap: rem(10);
}

.wp-pagenavi .previouspostslink {
  display: block;
  width: rem(38);
  height: rem(38);
  border: 1px solid $accent;
  position: relative;
  transition: .3s;
  margin-right: rem(40);
}

.wp-pagenavi .previouspostslink::before {
  content: "";
  width: rem(8);
  height: rem(16);
  background-image: url(../images/common/page-prev.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: .3s;
}

.wp-pagenavi .previouspostslink:hover {
  background-color: $accent;
}

.wp-pagenavi .previouspostslink:hover::before {
  background-image: url(../images/common/page-prev-hover.svg);
}

.wp-pagenavi .nextpostslink {
  display: block;
  width: rem(38);
  height: rem(38);
  border: 1px solid $accent;
  position: relative;
  transition: .3s;
  margin-left: rem(40);
}

.wp-pagenavi .nextpostslink::before {
  content: "";
  width: rem(8);
  height: rem(16);
  background-image: url(../images/common/page-next.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: .3s;
}

.wp-pagenavi .nextpostslink:hover {
  background-color: $accent;
}

.wp-pagenavi .nextpostslink:hover::before {
  background-image: url(../images/common/page-next-hover.svg);
}

.wp-pagenavi a, .wp-pagenavi span {
  display: inline-flex;
  width: rem(38);
  height: rem(38);
  background: #fff;
  border: 1px solid $accent;
  font-size: rem(16);
  font-family: $third-font-family;
  color: $accent;
  font-weight: 700;
  padding-top: rem(10);
  padding-right: rem(14);
  padding-bottom: rem(10);
  padding-left: rem(14);
  line-height: 1;
  transition: all 0.15s linear;
}
.wp-pagenavi a:hover{
  background-color: #408F95;
  color: $white;
  opacity: 1;
}
.wp-pagenavi span.current{
  background-color: #408F95;
  color: $white;
}
