@use 'global' as *;

.archive__wrap {
  margin-top: rem(27);
}

.archive__year {
  font-size: rem(24);
  font-weight: 400;
  line-height: calc(35 / 24);
  color: $accent;
  padding-left: rem(32);
  position: relative;
  cursor: pointer;
}

.archive__year::before {
  content: "";
  width: rem(24);
  height: rem(24);
  position: absolute;
  left: 0;
  top: 62%;
  transform: translateY(-50%) rotate(120deg);
  background: url(../images/common/triangle-close.png) no-repeat center center/contain;
  transition: .2s;
}

.archive__year.open::before {
  top: 10%;
  transform: rotate(210deg); // 下向きに変える
  transition: .2s;
}

.archive__year--close {
  position: relative;
}

.archive__year:nth-child(n+2) {
  margin-top: rem(5);
}

.archive__lists {
  margin-left: rem(26);
  margin-top: rem(2);
}

.archive__lists li {
  font-size: rem(24);
  font-weight: 400;
  line-height: calc(35 / 24);
  color: $accent;
  padding-left: rem(32);
  position: relative;
}

.archive__lists li::before {
  content: "";
  width: rem(24);
  height: rem(24);
  background: url(../images/common/triangle-close.png) no-repeat center center/contain;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.archive__lists li:nth-child(n+2) {
  margin-top: rem(3);
}