@use 'global' as *;

.privacy-policy {
  position: relative;
}

.privacy-policy::before {
  content: "";
  width: rem(120);
  height: rem(55);
  background-image: url(../images/common/illust_voice_1.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: rem(-24);
  left: max(rem(15), calc(50% - rem(620)));
  @include mq('md') {
    width: rem(301);
    height: rem(138);
    top: rem(-70);
  }
}

.inner.privacy-policy__inner {
  @include mq('md') {
    max-width: rem(850);
    width: 100%;
  }
}

.privacy-policy__title {
  font-size: rem(24);
  font-weight: 500;
  line-height: calc(35 / 24);
  @include mq('md') {
    font-size: rem(32);
    line-height: calc(46 / 32);
  }
}

.privacy-policy__wrap {
  margin-top: rem(40);
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(32 / 16);
}

.privacy-policy ol {
	list-style: revert;
	margin-left: 1.25rem;
}

.privacy-policy__wrap ol:nth-of-type(1) {
	margin-top: 2rem;
}

.privacy-policy__wrap p:not(:first-child) {
	margin-top: 2.125rem;
}